import {
  RouteObject,
  useRoutes,
  useNavigate,
  useLocation
} from 'react-router-dom';
import {
  Button,
  Container
} from 'react-bootstrap';
import { Starter, SearchBar, BarLoader } from './Component'
import { useEffect, useState } from 'react';

function App() {

  let routes: RouteObject[] = [
    {
      path: "/",
      element: <Search />
    },
    {
      path: "/result",
      element: <Result />
    },
    {
      path: "/load",
      element: <BarLoader />
    }
  ];

  let element = useRoutes(routes);

  return (
    <div>
      {element}
    </div>
  );
}

function Search() {
  return (
    <Container>
      <Starter />
      <SearchBar />
    </Container>
  );
}

function Result() {
  let navigate = useNavigate();
  const { state }: any = useLocation();
  const res = state.result;
  // const [result, setResult] = useState({});

  // function* ReturnSearch(): any {
  //   for (let i = 0; i < Object.keys(res).length; i++) {
  //     const obj = res[i]
  //     console.log(obj)
  //     yield obj;
  //   }
  // };

  // const process = ReturnSearch();
  // let resule = [];

  // function getNextResult() {
  //   const nextRes = process.value;
  //   const res = (
  //     <div>
  //       <p>
  //         <span>Link: </span>
  //         <a href={result.url}>{Result.url}</a>
  //       </p>
  //     </div>
  //   );
  // }

  // useEffect(() => { 
  //   getNextResult
  // }, []);

  return (
    <div>
      <Container>
        <Starter />
        {/* <ReturnSearch /> */}
        {
          Object.keys(res).map(function (key, index) {
            const data = res[key];
            console.log(data)
            return (
              <div id={data.index}>
                <p>
                  <span>Link: </span>
                  <a href={data.url}>{data.url}</a>
                </p>
                <hr />
                <p>{data.text}</p>
              </div>
            );
          })
        }
      </Container>
      <Button
        variant='success'
        className='full'
        onClick={() => navigate('/')}
      >
        Back
      </Button>
    </div>
  )
}

export default App;
